.b-splited {
  position: relative;
  background: white; }
  @media screen and (max-width: 768px) {
    .b-splited__title {
      display: none; } }
  .b-splited__title-copy {
    display: none;
    position: static !important;
    margin-bottom: 20px; }
    @media screen and (max-width: 768px) {
      .b-splited__title-copy {
        display: block; } }
  .b-splited__wrap {
    display: flex;
    width: 100%; }
    @media screen and (max-width: 768px) {
      .b-splited__wrap {
        display: block; } }
  .b-splited__block {
    min-height: 100vh;
    flex: 0 0 50%;
    min-width: 0;
    max-width: 50%; }
    @media screen and (max-width: 768px) {
      .b-splited__block {
        max-width: 100%;
        min-height: 0; } }
    .b-splited__block:nth-child(1) .b-splited__content {
      margin: 0 0 0 auto; }
  .b-splited__content {
    height: 100%;
    max-width: 565px;
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media screen and (max-width: 768px) {
      .b-splited__content {
        max-width: 100%;
        padding: 20px 20px 15px; } }
  .b-splited__slider {
    max-width: 50vw;
    max-height: 100vh; }
    @media screen and (max-width: 768px) {
      .b-splited__slider {
        max-width: 100%;
        height: 50vh; } }
    .b-splited__slider .item {
      height: 100vh;
      background: center/cover no-repeat; }
  .b-splited__btn-wrap {
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    bottom: 47px;
    z-index: 6; }
    .b-splited__btn-wrap .c-btn {
      min-width: 140px; }
